            <img class="aboutimg" :src="aboutimg" alt="关于">
            <p
                class="abouttext"
            >less is more 是著名的建筑师米斯、范德洛说过的一句话，意思是“少即是多”。这是一种提倡简单、反对过度装 饰的设计理念，他认为简单平凡的东西往往带给人们的是更多的享受。而Houzz则打破了大多数人被名言佳句禁锢的传统观念，以more is mo re 作为网站头衔进行宣传，网站内提供大量的资源供用户享用，将more is more的思想发挥的淋漓尽致，在以大胆创新为引领时代进步最佳因素之 一的现今社会，这种大胆的跳脱已经足够吸引更多人去了解这个网站。Houzz自称是“领先的网上家装和设计平台”，这种大胆的口号被国外多家媒体报道。它提供了 Pinterest风格的家居设计目录供用户浏览。此外你也可以查看Houzz上的数百篇文章，或者参与它的社区讨论。但Houzz最重要的功能是把业主和设计师、家装专业人士连 接到了一起。 由此可见，Houzz作为一种新的尝试，已经引起了社会上的反响与关注，这是一个时代的进步。</p>
