<script>
import reuqest from "@/utils/request";
export default {
    components: {},
    name: "HelloWorld",
    data() {
        return {
            aboutimg: "",
            text: ""
        };
    },
    methods: {},
    created() {
        // 获取about
        reuqest({
            url: "/api/getArticleDetaild",
            method: "post",
            data: {
                type: "about"
            }
        }).then(data => {
			// console.log(data)
            var str = data.data.data.content;
            // console.log(data.data.data.content)
            if (data.status == 200) {
                var str = data.data.data.content;
                var imgReg = /<img.*?(?:>|\/>)/gi;
                var srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/i;
                var arr = str.match(imgReg); // arr 为包含所有img标签的数组

                for (var i = 0; i < arr.length; i++) {
                    var src = arr[i].match(srcReg);
                    //获取图片地址
                    // console.log(src[1]);
                    this.aboutimg = src[1];
                }
            }
        });
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.html {
    background: #e6e6e6;
}
.about {
    display: inline-block;
    width: 1200px;
    background: white;
    margin: 0 auto;
    padding: 0 50px 20px;
}
h2 {
    font-size: 20px;
    padding-bottom: 10px;
    color: #333;
    border-bottom: 1px solid #999;
    margin-bottom: 20px;
    margin-top: 18px;
    text-align: left;
}
.aboutimg {
    margin-bottom: 10px;
}
.abouttext {
    text-align: left;
    margin-top: 0px;
    margin-bottom: 10px;
    padding: 0px;
    text-indent: 2em;
    font-size: 14px;
    color: rgb(51, 51, 51);
    line-height: 24px;
}
</style>



